.btn-cprimary{
    font-family: 'Helvetica Neue', sans-serif;
    border-radius: 4px;
    padding: 4.6px 18px;
    background-color: #7DA04C;
    text-transform: uppercase;
    text-align: center;
    font-size: 14px;
    color: #fff;
    @include transition();

    &:hover{
        color: #fff;
        background-color: darken($color-primary, 4);
    }
}

.btn-csecondary{
    font-family: 'Helvetica Neue', sans-serif;
    border-radius: 4px;
    padding: 4.6px 18px;
    background-color: $color-secondary;
    text-transform: uppercase;
    text-align: center;
    font-size: 14px;
